input{background-color:#f5f5f5;color:#000;padding:8px;border:1px solid #726e6e;border-radius:4px;font-size:14px}.header-container{width:100%;padding:10px 20px;background-color:#4d4d60;display:flex;flex-direction:row;justify-content:space-between;align-items:center}.header-container .Logo{color:#fff;font-size:24px;font-weight:700}.header-container .login-wrapper{display:flex;flex-direction:column;align-items:flex-end}.header-container .login-title{color:#fff;font-size:16px;margin-bottom:10px}.header-container .login-form{display:flex;align-items:center;gap:10px}.header-container .login-form .login-button{background-color:#856825;color:#fff;border:none;padding:8px 25px;border-radius:4px;cursor:pointer;transition:background-color .3s ease}.header-container .login-form .login-button:hover{background-color:#684f1f}.body-login{display:flex;flex-direction:row;justify-content:space-evenly;align-items:center;background-color:#bdb9b9;color:red}.body-login section{display:flex;flex-direction:column;justify-content:center;align-items:center;height:100%;width:50%;padding:100px 100px}.body-login .container-1{height:100%}.body-login .container-2 .form{display:flex;flex-direction:column;justify-content:center;align-items:center;gap:25px;width:300px;height:340px;padding:20px;background-color:#cdcdcd;border-radius:10px;box-shadow:0 0 10px 0 rgba(0,0,0,.1)}.body-login .container-2 .form .SignInForm{display:flex;flex-direction:column;justify-content:center;align-items:center;gap:10px;width:100%}